.program-form 
    background-color: $bg-dark-second
    padding: 0 0 100px
    position: relative
    @include tablet
        padding-bottom: 64px
    .light &
        background-color: $bg-light-second
    &::after
        content: ''
        position: absolute
        width: 32px
        height: 16px
        bottom: 0
        left: calc( 50% - 16px )
        mask-image: url('../img/partner-program/triangle.svg')
        mask-size: contain
        background-color: $dark
        .light &
            background-color: $bg-light-first
    &__inner 
        margin-top: 32px
        display: flex
        align-items: center
        @include tablet
            display: block
    &__left
        flex: 0 0 600px
        padding: 32px
        border-radius: 8px
        background-color: #192D4C
        position: relative
        @include tablet
            padding: 24px
        .light &
            background-color: $bg-light-first
        &::after
            content: ''
            position: absolute
            width: calc(100% - 64px)
            height: 8px
            left: 32px
            bottom: 0
            background-color: $orange
            border-radius: 8px 8px 0px 0px
            @include tablet
                width: calc(100% - 48px)
                left: 24px
    &__title
        font-size: 20px
        font-weight: 700
    &__text
        margin-top: 16px
        font-size: 14px
        color: $grey-l
        .light &
            color: $dark-grey
    &__callback
        margin-top: 24px
    &__row
        display: flex
        align-items: center
        width: 100%
        gap: 24px
        &--block
            display: block
        @include tablet
            display: block
        &:not(:first-child)
            margin-top: 16px
            @include tablet
                margin-top: 0
        &:last-child
            margin-top: 24px
    &__wrapper
        width: 100%
        position: relative
        @include tablet
            margin-top: 16px
    &__input
        padding: 15px 0 16px
        background-color: transparent
        border: 0
        outline: 0
        border-bottom: 1px solid $dark-grey
        font-size: 12px
        color: $white
        width: 100%
        .light &
            color: $dark
            border-bottom: 1px solid $secondary-light
        .error &
            border-bottom: 1px solid $red
    &__textarea
        padding: 15px 0 16px
        background-color: transparent
        border: 0
        outline: 0
        border-bottom: 1px solid $dark-grey
        font-size: 12px
        color: $white
        width: 100%
        resize: vertical
        height: 52px
        .light &
            color: $dark
            border-bottom: 1px solid $secondary-light
    &__label 
        position: absolute
        font-size: 12px
        left: 0
        top: 15px
        color: $main-secondary-text
        transition: top .1s linear
        .light &
            color: $grey-l
        .active &
            top: -3px
        .error &
            color: $red
    &__btn 
        padding: 14px 28px
        border-radius: 8px
        @include tablet
            width: 100%
        .program-faq__form &
            width: 100%
    &__conditions
        max-width: 256px
        font-size: 12px
        color: $grey-l
        @include tablet
            margin-top: 16px
            text-align: center
            max-width: 100%
        .program-faq__form &
            margin-top: 16px
            text-align: center
            max-width: 100%
        & a
            color: inherit
            text-decoration: underline
    &__right
        @include tablet
            margin-top: 32px