.partners-why
    padding: 48px 0
    @media screen and (max-width: 768px)
        padding: 32px 0
    &__items
        margin-top: 25px
        display: grid
        grid-template-columns: repeat(6, 1fr)
        gap: 24px
        list-style-type: none
        @media screen and (max-width: 1200px)
            grid-template-rows: 1fr 1fr
            grid-template-columns: repeat(3, 1fr)
        @media screen and (max-width: 768px)
            display: block
    &__item
        align-self: stretch
        padding: 24px 21px
        border-radius: 8px
        background: rgba(255, 255, 255, 0.05)
        color: #B1BED2
        text-align: center
        justify-content: center
        font-size: 14px
        display: flex
        align-items: center
        @media screen and (max-width: 768px)
            text-align: center
            &:not(:first-child)
                margin-top: 24px
                
