.program-possibility 
    background-color: $dark
    padding: 0 0 96px
    position: relative
    @include tablet
        padding-bottom: 64px
    .light & 
        background-color: $bg-light-first
    &::after
        content: ''
        position: absolute
        width: 32px
        height: 16px
        bottom: 0
        left: calc( 50% - 16px )
        mask-image: url('../img/partner-program/triangle.svg')
        mask-size: contain
        background-color: $bg-dark-second
        .light & 
            background-color: $bg-light-second
    &__inner 
        display: flex
        align-items: center
        @include tablet
            display: block
    &__left
        flex: 0 0 600px
    &__right
        & img
            max-width: 100%
        @include tablet
            margin-top: 32px
            text-align: center
    &__text 
        margin-top: 32px
        color: $grey-l
        font-size: 14px
        .light & 
            color: $dark-grey
    &__list
        margin-top: 32px
    &__item
        display: flex
        gap: 22px
        &:not(:first-child)
            margin-top: 16px
    &__img 
        flex: 0 0 64px
        width: 64px
        height: 64px
        border-radius: 8px  
        background-color: #192D4C    
        display: flex
        flex-direction: column 
        align-items: center
        justify-content: center
        position: relative
        &::before
            content: ''
            position: absolute
            width: 24px
            height: 24px
            top: 50%
            left: 50%
            transform: translate(-50%,-50%)
            background-color: $white
            mask-size: containt
            mask-repeat: no-repeat
            mask-position: center
            mask-image: url(../img/partner-program/possibility1.svg)
        &--second::before
            mask-image: url(../img/partner-program/possibility2.svg)
        &--third::before
            mask-image: url(../img/partner-program/possibility3.svg)
        .light & 
            background-color: #D6E4FA   
            &::before
                background-color: $blue-l
    &__description
        margin-top: 6px
        h6
            font-size: 20px
            font-weight: 700
        p
            font-size: 14px
            margin-top: 4px
            color: $grey-l
            .light & 
                color: $dark-grey



        