@import "advantages"
@import "descriptor"
@import "faq"
@import "form"
@import "possibility"
@import "steps"

.partner-program 
    color: $white
    line-height: 1.4
    .light & 
        color: $dark
.program-title
    position: relative
    overflow: visible
    padding-top: 96px
    @include tablet
        padding-top: 64px
    &--short
        padding-top: 0
        max-width: 440px
        & span
            display: none
    & h2    
        position: relative
        font-size: 40px
        font-weight: 700
        line-height: 1.4
        z-index: 2
        @media screen and (max-width: 1104px)
            font-size: 32px
    & span
        position: absolute
        z-index: 1
        bottom: 0
        left: 0
        color: $bg-dark-second
        text-shadow: -1px 0 #394262, 0 1px #394262, 1px 0 #394262, 0 -1px #394262
        font-size: 96px
        font-style: normal
        font-weight: 700
        line-height: 1
        @media screen and (max-width: 1104px)
            font-size: 72px
        @include tablet
            display: none
        &.dark
            color: $dark
        .light &
            color: $bg-light-second
            text-shadow: -1px 0 $secondary-light, 0 1px $secondary-light, 1px 0 $secondary-light, 0 -1px $secondary-light
            &.dark
                color: $bg-light-first
