.partners-map
    padding: 48px 0 96px
    @media screen and (max-width: 768px)
        padding: 32px 0 64px
    &__inner
        display: flex
        height: 524px
        margin-top: 24px
        border-radius: 8px
        background: #182841
        overflow: hidden
        @media screen and (max-width: 768px)
            flex-direction: column
            height: auto
    &__left
        padding: 24px
    &__search
        display: inline-block
        position: relative
        margin-bottom: 16px
        @media screen and (max-width: 768px)
            width: 100%
        &::after
            content: ''
            position: absolute
            width: 20px
            height: 20px
            mask-image: url(../img/market/search.svg)
            mask-position: center
            mask-repeat: no-repeat
            mask-size: 20px
            background-color: #69768B
            top: 14px
            right: 16px
            transform: rotate(270deg)
            pointer-events: none
    &__input
        width: 254px
        border-radius: 8px
        background: #1F2E47
        color: #ffffff
        border: 0
        outline: 0
        padding: 14px 40px 14px 16px
        font-size: 14px
        font-weight: 500
        &::placeholder
            color: #69768B
        @media screen and (max-width: 768px)
            width: 100%
    &__results
        overflow: auto
        overflow-x: hidden
        padding-right: 8px
        height: 400px
        &::-webkit-scrollbar
            width: 12px
            height: 12px
            background-color: #1F2E47
            border-radius: 8px
        &::-webkit-scrollbar-thumb
            background-color: #32435C
            border-radius: 8px
            box-shadow: 0
        @media screen and (max-width: 768px)
            height: 370px
            padding-right: 20px
    &__result
        &:not(:first-child)
            margin-top: 10px
    &__city
        font-size: 14px
        font-weight: 500
    &__list
        list-style-type: none
        margin-top: 8px
    &__item
        cursor: pointer
        width: 254px
        padding: 16px
        border-radius: 8px
        background: #1F2E47
        &:not(:first-child)
            margin-top: 8px
        @media screen and (max-width: 768px)
            width: 100%
    &__name
        font-size: 16px
        font-weight: 500
        pointer-events: none
    &__contact
        display: inline-block
        margin-top: 4px
        font-size: 14px
        font-weight: 500
        color: #B1BED2
        &:hover
            color: #89B0FD
    &__right
        flex: 1 0 auto
        @media screen and (max-width: 768px)
            height: 500px
    &__wrapper
        width: 100%
        height: 100%
        display: flex
        justify-content: flex-end
        