.partners-descriptor
    background: url('../img/partners/descriptor-bg.jpg') center no-repeat
    background-size: cover
    margin-top: -66px
    padding: 122px 0 96px
    color: #ffffff
    @media screen and (max-width: 1200px)
        margin-top: -81px
    @media screen and (max-width: 768px)
        padding: 88px 0 96px
    &__title
        font-size: 64px
        font-weight: 700
    &__subtitle
        margin-top: 8px
        font-size: 24px
        font-weight: 600
    &__inner
        display: flex
        margin-top: 24px
        gap: 24px
        @media screen and (max-width: 1024px)
            flex-direction: column
    &__block
        border-radius: 8px
        background: rgba(11, 26, 50, 0.30)
        display: flex
        flex-direction: column
        &:last-child
            flex: 0 0 400px
            @media screen and (max-width: 1024px)
                flex: initial
    &__text
        padding: 24px
        color: #B1BED2
        font-size: 14px
        font-weight: 500
        & p:not(:first-child)
            margin-top: 24px
        & h3
            color: #ffffff
            font-size: 18px
            font-weight: 700
            & + p:not(:first-child)
                margin-top: 16px
    &__link
        margin-top: auto
        display: block
        text-align: center
        padding: 14px 24px
        border-top: 1px solid #758FB9
        color: #ffffff
        font-size: 14px
        line-height: 1.4
        font-weight: 600
