@import "descriptor"
@import "steps"
@import "why"
@import "map"

.partners
    color: #ffffff
    &-gradient
        background: linear-gradient(180deg, #061429 0%, #162844 100%)
    &__title 
        font-size: 36px
        font-weight: 700
        max-width: 860px
    &__subtitle
        margin-top: 16px
        color: #B1BED2
        font-size: 14px
        font-weight: 500
        max-width: 588px
    &__title-block
        display: flex
        align-items: flex-end
        justify-content: space-between
        @media screen and (max-width: 768px)
            display: block
    &__title-link
        border-radius: 8px
        background: #E7EFFF
        padding: 14px 16px
        font-size: 14px
        font-weight: 600
        color: #07162B
        display: block
        margin-left: 10px
        @media screen and (max-width: 768px)
            display: inline-block
            margin-top: 32px
    &__breadcrumbs
        list-style-type: none
        display: flex
        align-items: center
        padding: 0
        color: rgba(255, 255, 255, 0.50)
        & a 
            color: rgba(255, 255, 255, 0.50)
        & li:not(:last-child)
            position: relative
            &::after
                content: '/'
                margin: 0 8px