.partners-steps
    padding: 96px 0 48px
    @media screen and (max-width: 768px)
        padding: 64px 0 32px
    &__items
        margin-top: 24px
        display: flex
        align-items: stretch
        gap: 24px
        list-style-type: none
        @media screen and (max-width: 768px)
            flex-direction: column
    &__item
        padding: 24px
        width: calc( 25% - 18px )
        height: auto
        display: flex
        flex-direction: column
        border-radius: 8px
        background: rgba(255, 255, 255, 0.05)
        @media screen and (max-width: 768px)
            width: 100%
    &__name
        font-weight: 600
        font-size: 16px
        position: relative
        &::before
            content: ''
            position: absolute
            border-radius: 0px 4px 4px 0px
            background: #89B0FD
            width: 8px
            height: 22px
            left: -24px
            top: -3px
    &__text
        margin-top: 16px
        color: #B1BED2
        font-weight: 500
        font-size: 14px
        margin-bottom: 36px
    &__number
        margin-top: auto
        color: #B1BED2
        font-weight: 500
        font-size: 14px
