.program-advantages
    background-color: $bg-dark-second
    padding: 0 0 100px
    position: relative
    @include tablet
        padding-bottom: 64px
    .light &
        background-color: $bg-light-second
    &::after
        content: ''
        position: absolute
        width: 32px
        height: 16px
        bottom: 0
        left: calc( 50% - 16px )
        mask-image: url('../img/partner-program/triangle.svg')
        mask-size: contain
        .light &
            background-color: $bg-light-first
    &::before
        content: ''
        position: absolute
        width: 32px
        height: 16px
        top: -16px
        left: calc( 50% - 16px )
        mask-image: url('../img/partner-program/triangle.svg')
        mask-size: contain
        background-color: $bg-dark-second
        .light &
            background-color: $bg-light-second
    &__list 
        margin-top: 32px
        list-style-type: none
        position: relative
    &__row 
        display: flex
        align-items: stretch
        gap: 24px
        &:not(:first-child)
            margin-top: 24px
        @include tablet
            align-items: initial
            flex-direction: column
    &__item 
        padding: 32px
        border-radius: 8px
        background: #1A2D4D
        align-self: stretch
        height: auto
        line-height: 1.4
        position: relative
        display: flex
        flex-direction: column
        width: calc( 33.3333% - 16px )
        overflow: hidden
        flex-shrink: 1
        @include tablet
            width: 100%
        .light &
            background: #E8F6F9
        &--animate
            transition: width .1s linear
            &::before
                content: ''
                position: absolute
                right: 0
                top: 0
                width: 32px
                height: calc( 100% - 150px )
                background-color: #1A2D4D
                z-index: 1
                .light &
                    background: #E8F6F9
            &::after
                content: ''
                position: absolute
                width: 64px
                height: calc( 100% - 175px )
                right: 32px
                top: 32px
                background: linear-gradient(270deg, #1A2D4D 0%, rgba(26, 45, 77, 0.00) 100%)
                z-index: 2
                .light &
                    background: linear-gradient(270deg, #E8F6F9 0%, rgba(232, 246, 249, 0.00) 100%)
            &:hover
                &::before, &::after
                    display: none
                width: calc( 50% - 16px )
                flex-shrink: 0
                @include tablet
                    width: 100%
                & .program-advantages__link
                    opacity: 1
    &__title
        font-size: 20px
        font-weight: 700
        position: relative
        .program-advantages__item--animate &
            font-size: 32px
            @include tablet
                font-size: 20px
        &::before
            position: absolute
            content: ""
            left: -32px
            top: 0
            height: 28px
            width: 8px
            border-radius: 0px 8px 8px 0px
            background-color: $orange
            .program-advantages__item--animate &
                height: 45px
                width: 8px
                @include tablet
                    height: 28px
                    width: 8px
        .program-advantages__item--animate &
            width: 524px
            @media screen and (max-width: 1200px)
                width: 414px
            @include tablet
                width: auto
    &__text
        margin-top: 16px
        font-size: 14px
        color: $grey-l
        .light &
            color: $dark-grey
        .program-advantages__item--animate &
            margin-top: 24px
            margin-bottom: 68px
            width: 524px
            @media screen and (max-width: 1200px)
                width: 414px
            @include tablet
                width: auto
                margin-bottom: 0
    &__link
        align-self: flex-start
        margin-top: auto
        display: inline-block
        padding: 17px 32px
        font-size: 14px
        border-radius: 8px
        border: 1px solid $main-secondary-text
        color: $white
        position: relative
        z-index: 2
        opacity: 0
        @include tablet
            display: none
        @include hover
            background-color: $hover-blue-l
            border: 1px solid $hover-blue-l
            color: $white
        &:active
            border: 1px solid $active-blue-l
            background-color: $active-blue-l
            color: $white
        .light &
            color: $main-secondary-text
            @include hover
                color: $white
    &__img
        position: absolute
        bottom: 0
        right: 0
        z-index: 1
        @include tablet
            display: none