.breadcrumbs
    list-style-type: none
    display: flex
    align-items: center
    padding: 0
    color: rgba(255, 255, 255, 0.50)
    flex-wrap: wrap
    row-gap: 4px
    & li
        color: $white
        font-size: 12px
        .light & 
            color: $dark
        &:not(:last-child)
            color: rgba(255, 255, 255, 0.50)
            position: relative
            .light & 
                color: $active-secondary
            &::after
                content: '/'
                margin: 0 8px
        & a 
            color: inherit
