.program-steps  
    background-color: $dark
    padding: 0 0 96px
    @include tablet
        padding-bottom: 64px
    .light &
        background-color: $bg-light-first
    &__list
        display: flex
        align-items: stretch
        margin-top: 32px
        list-style-type: none
        gap: 24px
        padding: 0
        @include tablet
            display: grid
            grid-template-columns: repeat(3, 290px)
            padding-bottom: 24px
            width: calc( 100% + 16px )
            overflow-x: auto
    &__item 
        width: calc( 33.3333% - 16px )
        padding: 32px
        border: 1px solid $dark-grey
        display: flex
        flex-direction: column
        align-self: stretch
        border-radius: 8px
        @include tablet
            width: 100%
            padding: 24px
        .light &
            border: 1px solid $secondary-light
        &:first-child
            border: 1px solid $orange
            background-color: $orange
            & .program-steps__number
                color: $orange
                background-color: $white
                &::before
                    border: 1px solid $white
            &   .program-steps__title::after
                background-color: $white
            & .program-steps__link
                color: $orange
                border: 1px solid $white
                background-color: $white
            & .program-steps__text 
                color: $white
    &__number
        align-self: flex-start
        font-size: 16px
        font-weight: 700
        color: $white
        padding: 9px
        min-width: 40px
        min-height: 40px
        border-radius: 4px
        position: relative
        background-color: $orange
        margin-bottom: 8px
        text-align: center
        &::before 
            content: ""
            position: absolute
            top: -8px
            left: -8px
            width: calc(100% + 16px)
            height: calc(100% + 16px)
            border: 1px solid $orange
            border-radius: 6px
    &__title 
        font-size: 20px
        font-weight: 700
        margin-top: 32px
        padding-bottom: 16px
        position: relative
        &::after
            content: ''
            position: absolute
            bottom: 0
            left: 0
            width: 64px
            height: 1px
            background-color: $orange
    &__text
        margin-top: 16px
        font-size: 14px
        color: $grey-l
        margin-bottom: 32px
        .light &
            color: $dark-grey
    &__link
        align-self: flex-start
        margin-top: auto
        color: $white
        font-size: 14px
        font-weight: 500
        padding: 12px 24px
        border-radius: 6px
        border: 1px solid $main-secondary-text
        .light &
            color: $main-secondary-text
