.program-descriptor
    padding: 168px 0 120px
    margin-top: -72px
    background: url(../img/partner-program/descriptor.png) center no-repeat
    background-size: cover
    @include tablet
        padding: 136px 0 64px
        margin-top: -72px
    .light & 
        background: linear-gradient(90deg, #E8F6F9 0%, rgba(232, 246, 249, 0.00) 50%, #E8F6F9 100%), url(../img/partner-program/descriptor-light.png), lightgray 50% 
        background-size: cover
    &__title
        margin-top: 8px
        max-width: 870px
        font-size: 48px
        font-weight: 700
        line-height: 1.4
        position: relative
        padding-bottom: 24px
        @include tablet
            font-size: 32px
        &::before
            content: ''
            position: absolute
            bottom: 0
            left: 324px
            height: 1px
            width: 100px
            background-color: $orange
            @include tablet
                width: 50px
                left: 174px
        &::after
            content: ''
            position: absolute
            bottom: 0
            left: 0
            height: 1px
            width: 300px
            background-color: $orange
            @include tablet
                width: 150px
    &__text
        margin-top: 32px
        max-width: 730px
        color: $grey-l
        font-size: 14px
        line-height: 1.4
        .light & 
            color: $dark-grey
    &__btns
        margin-top: 40px
        display: flex
        align-self: center
        gap: 16px
        @include tablet
            flex-direction: column
            align-items: flex-start
    &__btn
        padding: 17px 32px
        border-radius: 8px
        font-size: 14px
        font-weight: 500
        line-height: 1.4
        display: flex
        align-items: center
        & svg 
            width: 16px
            height: 16px
            margin-left: 8px
        &.btn--border
            .light &
                color: $main-secondary-text
                border: 1px solid $active-secondary
                @include hover 
                    color: $white
                    border: 1px solid $hover-blue-l