.program-faq 
    background-color: $dark
    padding: 0 0 100px
    position: relative
    @include tablet
        padding: 0 0 64px
    .light & 
        background-color: $bg-light-first
    &__inner
        margin-top: 32px
        display: flex
        align-items: flex-start
        gap: 36px
        @include tablet
            flex-direction: column
    &__subtitle
        font-size: 14px
        color: $grey-l
        .light &
            color: $dark-grey
        & a
            color: $orange
            text-decoration: underline
    &__items 
        margin-top: 24px
        list-style-type: none
        border-bottom: 1px solid #304564
        .light & 
            border-bottom: 1px solid $secondary-light
    &__item
        padding: 24px 0
        border-top: 1px solid #304564
        cursor: pointer
        .light & 
            border-top: 1px solid $secondary-light
        &.open
            cursor: default
    &__question
        font-weight: 700
        font-size: 14px
        padding-left: 40px
        position: relative
        &::before
            content: ''
            position: absolute
            left: 0
            top: -3px
            width: 24px
            height: 24px
            mask-image: url('../img/partner-program/plus.svg')
            mask-position: center
            mask-repeat: no-repeat
            mask-size: contain
            background-color: #304564
            .light &
                background-color: $active-secondary
        .open &::before
            mask-image: url('../img/partner-program/minus.svg')
    &__answer
        height: 0
        max-height: 0
        transition: all .1s linear
        color: $grey-l
        font-size: 14px
        padding-left: 40px
        overflow: hidden
        .light &
            color: $dark-grey
        .open &
            margin-top: 26px
            height: auto
            max-height: 1000px
    &__form
        flex: 0 0 384px
        background-color: #1A2D4D
        border-radius: 8px
        padding: 32px
        @include tablet
            padding: 24px
        .light & 
            border: 1px solid $secondary-light
            background-color: $bg-light-first
    &__title 
        font-size: 20px
        font-weight: 700
    &__subtitle
        margin-top: 16px
        font-size: 14px
        color: $grey-l
        .light &
            color: $dark-grey
    
